body {
    background-color: #e4f9f5;
    font-family: 'Do Hyeon', sans-serif;
}

header {
}

.screenshoot-image {
    margin: 25px 50px 25px 50px;
    width: 300px;
}

.body-container {
    display: flex;
    flex-wrap: wrap-reverse;
    justify-content: center;
    align-items: center;
}

.description {
    margin-top: 25px;
    margin-bottom: 25px;
}

.description .icon-image {
    height: 80px;
    margin-right: 10px;
}

.description .definition {
    display: inline-block;
}

.description dt {
    font-size: 50px;
    color: #11999e;
}

.description dd {
    margin-left: 0px;
    color: #11999e;
}

.playstore-image {
    height: 60px;
}

.appstore-image {
    margin: 10px;
    height: 40px
}

.footer {
    text-align: center;
}